.button {
    background-color: #fff;
    border: 1px solid #222;
    color: #222;
    cursor: pointer;
    display: inline-block;
    font-family: sans-serif;
    font-size: 15px;
    font-weight: bold;
    line-height: 20px;
    padding: 10px 40px;

    &:hover {
        background-color: #222;
        color: #fff;
    }
}



